.addBtn {
  padding: 10px;
  width: 10%;
  background: blue;
  color: white;
  float: right;
  text-align: center;
  font-size: 16px;
  cursor: pointer;
  transition: 0.3s;
  border-radius: 5px;
}

.addBtn:hover {
  background-color: rgb(40, 30, 187);
}

input {
  margin: 0;
  border: none;
  border-radius: 5px;
  width: 75%;
  padding: 10px;
  float: right;
  font-size: 16px;
}

.header {
  background-color: #3a4cee;
  padding: 30px 40px;
  color: white;
  text-align: center;
  height: 300px;
}

.get-work {
  text-align: center;
}

.text {
  text-align: right;
}

.btn1 {
  font-size: 20px;
  color: red;
  border-radius: 5px;
  margin-left: 20px;
}
.text-undo {
  text-decoration: line-through
}
.text-normal {
  text-decoration: normal
}